.banner {
    position: relative;
    min-height: 720px;
    margin-bottom: 95px;
    @include until-desktop {
        margin-bottom: 60px; }
    @include while-mob {
        min-height: 100vh; }
    & .icon-img-blue-bg {
        z-index: -1;
        transform: rotate(-124deg);
        width: 947px;
        height: 1010px;
        top: 84px;
        fill: #ffd155;
        left: -720px;
        position: absolute;
        @include until-desktop {
            display: none; } }
    &__video {
        position: absolute;
        top: 0;
        right: 0;
        width: 955px;
        height: 725px;
        background: #204B67;
        @include media-until(1600px) {
            right: -150px; }
        @include media-until(1400px) {
            right: -300px;
            top: -100px; }
        @include media-until(1100px) {
            width: 830px; }
        @include until-desktop {
            width: 100vw;
            top: 0;
            right: 0; }
        @include while-mob {
            height: 100%; }
        &-play {
            position: absolute;
            bottom: 140px;
            left: 47px;
            z-index: 5;
            width: 120px;
            height: 120px;
            @include until-desktop {
                left: 16px;
                bottom: 20px; }
            @include until-tablet {
                transform: scale(0.8); }
            &:hover {
                svg {
                    fill-opacity: 1; } }
            & svg {
                transition: fill-opacity .3s;
                fill-opacity: .4;
                width: 100%;
                height: 100%; } }
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover; }
        &::before {
            z-index: 1;
            position: absolute;
            bottom: 0;
            left: -1px;
            width: 1500px;
            height: 1500px;
            background: url('../../assets/img/banner-video-mask.svg') no-repeat bottom left;
            content: '';
            @include media-until(1100px) {
                width: 1300px;
                height: 1300px; }
            @include until-desktop {
                display: none; } } }
    &__content {
        z-index: 2;
        margin-top: 287px;
        align-self: flex-start;
        width: 100%;
        max-width: 509px;
        padding-bottom: 150px;
        @include media-until(1600px) {
            margin-top: 200px; }
        @include media-until(1200px) {
            max-width: 480px; }
        @include until-desktop {
            margin-top: 180px; }
        & * {
            @include until-desktop {
                color: white;
                text-shadow: 1px 1px 2px black; } } }
    &__title {
        text-align: left;
        margin-bottom: 40px; } }
