.examples {
    position: relative;
    margin-bottom: 196px;
    @include until-tablet {
        margin-bottom: 100px; }

    & .icon-img-blue-bg {
        z-index: -1;
        transform: rotate(1deg);
        width: 947px;
        height: 993px;
        top: -101px;
        fill: $blue;
        left: -751px;
        position: absolute;
        @include media-until(1600px) {
            left: -850px; }
        @include media-until(1440px) {
            display: none; } }
    &__title {
        margin-bottom: 90px;
        max-width: 635px;
        @include until-tablet {
            margin-bottom: 60px; } }
    &__slider {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        width: 100%;
        &-button {
            @include media-until(1366px) {
                position: static; } }
        & .swiper-container {
            width: 100%;
            @include media-until(1235px) {
                width: 100vw; } }
        & > .examples__slider-pagination.swiper-pagination {
            bottom: -65px;
            & .swiper-pagination-bullet {
                width: 6px;
                height: 6px;
                &:not(:last-of-type) {
                    margin-right: 11px; } } }
        &-buttons {
            display: flex;
            align-items: center;
            @include media-until(1366px) {
                margin-bottom: 30px; }
            & .swiper-button {

                &-next {
                    right: -81px;
                    @include media-until(1366px) {
                        margin-left: 15px;
                        position: static; }
                    &::after {
                        font-size: 26px;
                        font-weight: bold; } }
                &-prev {
                    left: -81px;
                    @include media-until(1366px) {
                        position: static; }
                    &::after {
                        font-size: 26px;
                        font-weight: bold; } } } } }
    &__slide {
        border-radius: 10px;
        overflow: hidden;
        max-width: 278px;
        margin-right: 30px; } }
