// FANCYBOX
body {
    font-family: 'Gotham Pro';
    padding: 0;
    margin: 0;
    color: #000000;
    font-size: 1rem;
    & * {
        box-sizing: border-box; } }

html {
    scroll-behavior: smooth;
    @include until-tablet {
 }        // font-size: 14px
    & .fancybox-slide {
        @include until-tablet {
            padding: 16px; } } }

select, input, textarea, button {
    font-family: 'Gotham Pro'; }

ul, ol, h1, h2, h3, h4, a {
    color: #000000;
    list-style: none;
    padding: 0;
    margin: 0;
    text-decoration: none; }

.wrap {
    position: relative;
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column; }

.wrapper {
    width: 100%;
    max-width: 1232px;
    margin: 0 auto;
    position: relative;
    padding-left: 1rem;
    padding-right: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center; }


// buttons, inputs=================================================================================================================================

a, button, select, input, label {
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-tap-highlight-color: transparent; }

button {
    padding: 0;
    cursor: pointer;
    background: none;
    border: none;
    &:active {
        outline: none; }
    &:focus {
        outline: none; } }

input, textarea {
    border: none; }
a {
    &:active {
        outline: none; }
    &:focus {
        outline: none; } }

input:focus, input:active, select:focus, select:active, textarea:focus, textarea:active {
    outline: none; }

// text==============================================================================================================================================
p {
    margin: 0;
    padding: 0;
    font-size: 1rem;
    line-height: 150%;
    font-weight: normal;
    @include until-tablet {
        font-size: 0.875rem; } }
h2 {
    font-size: 2.5rem;
    line-height: 120%;
    text-align: center;
    @include while-mob {
        font-size: 2rem; }
    @include media-until(374px) {
        font-size: 1.75rem; } }
